<template>
<div class="first-login">
  <img class="logo" src="@/assets/Logo2.png" alt="Pacha-Q-Tec Logo">
  <div>
    <h2>Cuéntanos sobre ti</h2>
    <h3>esta información la podrán ver las demás personas</h3>
    <form action="#" @submit.prevent="handleSubmit">
      <Input
        label="Perfil"
        :value="about"
        @inputChange="(value) => about = value"
      />
      <Input
        label="Edad"
        input-type="number"
        :value="age"
        @inputChange="(value) => age = value"
      />
      <Input
        label="Celular"
        input-type="tel"
        :value="phone"
        @inputChange="(value) => phone = value"
      />
      <Input
        label="Programa de especialización"
        :value="espe"
        @inputChange="(value) => espe = value"
      />
      <Input
        label="Sede"
        :value="campus"
        @inputChange="(value) => campus = value"
      />
      <Button btn-type="cta" class="btn--primary Button">Continuar</Button>
    </form>
  </div>
</div>
</template>

<script>
import Button from '@/components/Button.vue';
import Input from '@/components/Input.vue';
import { mapActions, mapState } from 'vuex';

export default {
  name: 'FirstLogin',
  components: {
    Button,
    Input,
  },
  data() {
    return {
      about: '',
      espe: '',
      phone: '',
      campus: '',
      age: '',
    };
  },
  watch: {
    user(value) {
      if (!value.firstTime) this.$router.replace('/');
      else alert('Try again.');
    },
  },
  computed: {
    ...mapState({
      user: (state) => state.authStore.user,
    }),
  },
  methods: {
    ...mapActions({
      updateProfile: 'authStore/updateProfile',
    }),
    handleSubmit() {
      this.updateProfile({
        id: this.user.id,
        about: this.about,
        espe: this.espe,
        phone: this.phone,
        campus: this.campus,
        age: this.age,
      });
    },
  },
};
</script>

