<template>
  <div class="input-container">
    <label class="label">{{label}}</label>
    <input
      class="input"
      :class="{ 'input--readonly' : nonEditable }"
      :type="type"
      :placeholder="inputPlaceholder"
      :readonly="nonEditable"
      @input="updateInput"
      :value="value"
      required
    >
    <div
      v-if="inputType == 'password'"
      class="password"
      :class="{ 'password--see' : seePassword }"
      @click="togglePassVisibility"
    >
      <img src="../assets/see.svg" alt="Watch password icon">
      <img src="../assets/no-see.svg" alt="Watch password icon">
    </div>
  </div>
</template>

<script>
export default {
  name: 'Input',
  props: {
    label: String,
    value: [Number, String],
    inputType: {
      type: String,
      default: 'text',
    },
    inputPlaceholder: {
      type: String,
      default: 'Escriba aquí...',
    },
    nonEditable: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      seePassword: false,
      type: this.inputType,
    };
  },
  methods: {
    togglePassVisibility() {
      this.seePassword = !this.seePassword;
      if (this.type === this.inputType) this.type = 'text';
      else this.type = this.inputType;
    },
    updateInput(e) {
      this.$emit('inputChange', e.target.value);
    },
  },
};
</script>

<style lang="scss" scoped>
@import '../assets/styles/main';

.input-container {
  position: relative;

  width: 100%;

  .password {
    position: absolute;
    bottom: 0;
    right: 0;

    height: calc(100% - 1.25em);
    display: flex;
    align-items: center;
    padding: .5em;

    cursor: pointer;

    img:last-child { display: none; }

    &--see {
      img:first-child { display: none; }
      img:last-child { display: block; }
    }
  }
}

.label {
  display: block;
  margin-bottom: .5em;

  font: $text-sm $font-secondary;
  font-weight: $bold;
  color: #BAB8CC;
  text-transform: uppercase;
}

.input {
  width: 100%;
  padding: 1rem;

  border: 1.2px solid #BAB8CC;
  border-radius: 5px;

  &::placeholder {
    color: $color-gray;
    font: $text-mdsm $font-secondary;

    opacity: 1;
  }

  &:focus {
    border-color: #5640FF;
    outline: none;
  }

  &--readonly {
    color: $color-gray;

    background-color: $color-lightgray;
    border-color: $color-lightgray;

    &:focus { border-color: inherit; }
  }
}
</style>
